<template>
  <div class="area-task-container">
    <div class="table-box">
      <div class="my-tab-header">
        <div class="my-tab-header-title">{{this.value4[0].name}}浓度与排名变化</div>
      </div>
      <!-- 表格 -->
      <div class="my-tab-body">

        <div class="block">
          <div class="block-left">
            <el-cascader v-model="value1" :options="options1"></el-cascader>
            <el-button type="primary" @click="ant">查询</el-button>
          </div>
          <div class="block-right">
            <el-cascader placeholder="魏善庄镇" v-model="value4" :options="options4" @change="handleChange"></el-cascader>
          </div>
        </div>
        <div class="my-table-body-content">
          <div class="my-table-header">
            <vue-seamless-scroll :data="list" class="seamless-warp" :class-option="classOption">
              <div class="my-table-header-right">
                <div v-for="item in list" :key="item.datetime">
                  <div class="right-top">{{ item.datetime || 0 }}</div>
                  <div class="right-center">
                    <div>浓度</div>
                    <div>排名</div>
                    <div>环比</div>
                  </div>
                  <div class="right-bottom">
                    <div>
                      <div v-if="item.value > 0 && item.value <= 50" :style="{ background: '#00E600' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value > 50 && item.value <= 100" :style="{ background: '#FFFF00' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value > 100 && item.value <= 150" :style="{ background: '#FF7F00' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value > 150 && item.value <= 200" :style="{ background: '#FF0000' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value > 200 && item.value <= 300" :style="{ background: '#9A004A' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value > 300 && item.value <= 9999999" :style="{ background: '#800021' }">
                        {{ item.value || 0 }}
                      </div>
                      <div v-else-if="item.value < 0" :style="{ background: '#666' }">
                        {{ item.value || "--" }}
                      </div>
                    </div>
                    <div>{{ item.rank || 0 }}</div>

                    <div v-if="JSON.stringify(item.mom)[0] == '-'">
                      <img :style="{ width: '13px' }" src="../../assets/down1.png" />
                    </div>
                    <div v-else-if="JSON.stringify(item.mom)[0] != '-'">
                      <img :style="{ width: '13px' }" src="../../assets/up1.png" />
                    </div>
                  </div>
                </div>
              </div>
            </vue-seamless-scroll>
          </div>

          <div class="my-table-box">
            <div class="my-table-box-text">排名变化图</div>
            <div class="my-table-box-echarts" id="main"></div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import { http } from "../../utils/request.js";
import * as echarts from "echarts";
export default {
  props: ["tableData"],
  data() {
    return {
      DistrictTownTab: "区",
      value1: ["134004", "4", "最近一年"],
      options1: [
        {
          value: "134004",
          label: "PM2.5",
          children: [
            {
              value: "4",
              label: "月",
              children: [
                {
                  value: "最近一年",
                  label: "最近一年",
                },
                {
                  value: "最近半年",
                  label: "最近半年",
                },
              ],
            },
            {
              value: "2",
              label: "日",
              children: [
                {
                  value: "最近三个月",
                  label: "最近三个月",
                },
                {
                  value: "最近一个月",
                  label: "最近一个月",
                },
              ],
            },
            {
              value: "1",
              label: "小时",
              children: [
                {
                  value: "最近三天",
                  label: "最近三天",
                },
                {
                  value: "最近一天",
                  label: "最近一天",
                },
              ],
            },
          ],
        },
        {
          value: "134001",
          label: "TSP",
          children: [
            {
              value: "4",
              label: "月",
              children: [
                {
                  value: "最近一年",
                  label: "最近一年",
                },
                {
                  value: "最近半年",
                  label: "最近半年",
                },
              ],
            },
            {
              value: "2",
              label: "日",
              children: [
                {
                  value: "最近三个月",
                  label: "最近三个月",
                },
                {
                  value: "最近一个月",
                  label: "最近一个月",
                },
              ],
            },
            {
              value: "1",
              label: "小时",
              children: [
                {
                  value: "最近三天",
                  label: "最近三天",
                },
                {
                  value: "最近一天",
                  label: "最近一天",
                },
              ],
            },
          ],
        },
        {
          value: "199054",
          label: "TVOC",
          children: [
            {
              value: "4",
              label: "月",
              children: [
                {
                  value: "最近一年",
                  label: "最近一年",
                },
                {
                  value: "最近半年",
                  label: "最近半年",
                },
              ],
            },
            {
              value: "2",
              label: "日",
              children: [
                {
                  value: "最近三个月",
                  label: "最近三个月",
                },
                {
                  value: "最近一个月",
                  label: "最近一个月",
                },
              ],
            },
            {
              value: "1",
              label: "小时",
              children: [
                {
                  value: "最近三天",
                  label: "最近三天",
                },
                {
                  value: "最近一天",
                  label: "最近一天",
                },
              ],
            },
          ],
        },
      ],
      // startime: this.$moment(new Date()).subtract(1,'months').format('YYYY-MM-01 00:00:00'),
      startime: this.$moment()
        .subtract(1, "years")
        .format("YYYY-MM-01 00:00:00"),
      endtime: this.$moment().format("YYYY-MM-01 00:00:00"),
      // 表头数据
      token:
        "qiYrxXEv5mJPW6-dU5_wQQQivUkpjzt0ZChwA8M9JwYNNGXuecoz2NC4MuGBeBqWNBGxCD_9rXfkYMy7rQYCkDBK3eLyAZh7MrUp0sPHisiOsrJZ4-Webgiz4c2KFWXLGxsPDXqKsxULZRgkYsx_yeSmBsOKzTWNLAax1-JzQvVE3ge7QEDrEhlNk7DeItdcgUelGUMqUvj7kImu2gUS3Q==",
      rankid: "82",
      timeType: "4",
      varType: 134004,
      auditflag: "0",

      // 表格数据
      myChart: null,
      myOptions: {},
      list: [],
      timeList: [],
      thisYear: [],
      lastYear: [],
      qoq: [],
      mom: [],
      end: 0,

      value4: [
        {
          adm_id: 110115001,
          adm_level: 4,
          rank_id: 82,
          name: "魏善庄镇",
        },
      ],
      options4: [],
      selectedOptions: {
        label: "魏善庄镇",
        value: {
          adm_id: 110115001,
          adm_level: 4,
          rank_id: 82,
        },
      },
    };
  },
  created() {
    this.getCity(); //获取城市
    this.getTableDataList();
    this.getEchart();
    // 初始化图表
    this.initCharts();
  },
  mounted() {
    // 初始化图表
    this.initCharts();
    this.handleChange();
    console.log("326 this.value4", this.value4);
  },
  updated() {
    // 初始化图表
    this.initCharts();
  },
  watch: {
    echartsData(newVal) {
      this.echartsData = newVal;
      this.initCharts();
    },
  },
  methods: {
    getCity() {
      let json = {
        token: this.token,
        rankId: "82",
      };
      http
        .get(
          `https://api-adm-data-service-pre.airqualitychina.cn:9998/adm/rank-info`,
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          // console.log(res.data.result,'rescity')
          if (JSON.stringify(res.data.result[0].adm_info) != "[]") {
            //   {value: '134004',label: 'PM2.5',}
            res.data.result[0].adm_info.map((item) => {
              this.options4 = [
                ...this.options4,
                {
                  label: item.adm_name,
                  value: {
                    adm_id: item.adm_id,
                    adm_level: item.adm_level,
                    rank_id: item.rank_id,
                    name: item.adm_name,
                  },
                },
              ];
            });
          }
        });
    },

    handleChange(value, e, r) {
      console.log("373 value", value, e, r);
      this.getTableDataList();
    },

    ant() {
      this.varType = this.value1[0];
      if (this.value1[1] == 4 && this.value1[2] == "最近一年") {
        //月
        this.timeType = 4;
        this.startime = this.$moment()
          .subtract(1, "years")
          .format("YYYY-MM-01 00:00:00"); //前一年
        this.endtime = this.$moment().format("YYYY-MM-01 00:00:00");
      }
      if (this.value1[1] == 4 && this.value1[2] == "最近半年") {
        //月
        this.timeType = 4;
        this.startime = this.$moment(new Date())
          .subtract(6, "months")
          .format("YYYY-MM-01 00:00:00"); //前半年
        this.endtime = this.$moment().format("YYYY-MM-01 00:00:00");
      }
      if (this.value1[1] == 2 && this.value1[2] == "最近三个月") {
        //日
        this.timeType = 2;
        this.startime = this.$moment(new Date())
          .subtract(3, "months")
          .format("YYYY-MM-DD 00:00:00");
        this.endtime = this.$moment().format("YYYY-MM-DD 00:00:00");
      }
      if (this.value1[1] == 2 && this.value1[2] == "最近一个月") {
        //日
        this.timeType = 2;
        this.startime = this.$moment(new Date())
          .subtract(1, "months")
          .format("YYYY-MM-DD 00:00:00");
        this.endtime = this.$moment().format("YYYY-MM-DD 00:00:00");
      }
      if (this.value1[1] == 1 && this.value1[2] == "最近三天") {
        //小时
        this.timeType = 1;
        this.startime = this.$moment()
          .subtract(3, "days")
          .format("YYYY-MM-DD 00:00:00");
        this.endtime = this.$moment().format("YYYY-MM-DD HH:00:00");
      }
      if (this.value1[1] == 1 && this.value1[2] == "最近一天") {
        //小时
        this.timeType = 1;
        this.startime = this.$moment()
          .subtract(1, "days")
          .format("YYYY-MM-DD 00:00:00");
        this.endtime = this.$moment().format("YYYY-MM-DD HH:00:00");
      }
      console.log(this.options4, "options4");
      // console.log(this.value1,'this.value1')
      this.getTableDataList();
    },

    getTableDataList() {
      let json = {
        token: this.token,
        rankid: this.rankid,
        timeType: this.timeType,
        varType: this.varType,
        startime: this.startime,
        endtime: this.endtime,
        auditflag: this.auditflag,
      };
      http
        .get(
          "https://api-adm-data-service-pre.airqualitychina.cn:9998/adm/center-rank",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          if (JSON.stringify(res.data.result) != "[]") {
            res = res.data.result;
            console.log("454 value4", this.value4);

            if (this.value4[0] == undefined) {
              this.list = res.filter(
                (item) => item.countyname == "大兴区" && item.admid == 110115001
              )[0].data;
            } else {
              this.list = res.filter(
                (item) =>
                  item.countyname == "大兴区" &&
                  item.admid == this.value4[0].adm_id
              )[0].data;
              console.log("465 this.list", this.list);
            }
          } else {
            this.list = [];
          }
        });
    },

    getColor(num) {
      if (num > 0 && num < 35) {
        return "#64C84C";
      } else if (num > 35 && num < 75) {
        return "#CBCD49";
      } else if (num > 75 && num < 115) {
        return "#C97C3D";
      } else if (num > 115 && num < 150) {
        return "#";
      } else if (num > 150 && num < 250) {
        return "#";
      } else if (num > 250 && num < 500) {
        return "#";
      } else {
        return "#666";
      }
    },

    getEchart() {
      let json = {
        token: this.token,
        rankid: "7",
        timeType: "4",
        varType: 134004,
        startime: this.$moment(new Date())
          .subtract(6, "months")
          .format("YYYY-MM-DD 00:00:00"),
        endtime: this.$moment().format("YYYY-MM-DD 00:00:00"),
        auditflag: "0",
        is_round: "True",
      };
      http
        .get(
          `https://api-adm-data-service-pre.airqualitychina.cn:9998/adm/contrast`,
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          // console.log(res.data.result,'res')
          if (JSON.stringify(res.data.result) != "[]") {
            res = res.data.result;
            let vardata = res.find((item) => item.admid == 4).data;
            let end = 100;
            if (vardata.length > 70) {
              end = 50;
            }
            let timeList = [];
            let lastYear = [];
            let thisYear = [];
            let qoq = []; //同比
            let mom = []; // 环比
            vardata.map((item) => {
              timeList.push(item.datetime);
              thisYear.push(item.rank || "-");
              lastYear.push(item.qoq.rank || "-");
              qoq.push(item["qoq-rank"] || "-");
              mom.push(item["mom-rank"] || "-");
            });
            // console.log(timeList,'timeList')
            // timeList = timeList.map((o) => {
            //   if (config.type == 'hourly') {
            //     return new Date(o).format('yyyy-MM-dd hh:00');
            //   } else if (config.type == 'daily') {
            //     return new Date(o).format('yyyy-MM-dd');
            //   } else if (config.type == 'monthly') {
            //     return new Date(o).format('yyyy-MM');
            //   }
            // });
            // // getBar(timeList, thisYear, lastYear, qoq, mom, end);
            // console.log(timeList, thisYear, lastYear, qoq, mom, end,'timeList, thisYear, lastYear, qoq, mom, end')
            this.timeList = timeList;
            this.thisYear = thisYear;
            this.lastYear = lastYear;
            this.qoq = qoq;
            this.mom = mom;
            this.end = end;
            // console.log(timeList,'--timeList', thisYear,'--thisYear', lastYear,'--lastYear', qoq,'--qoq', mom,'--mom', end,'--end')
          }
        });
    },

    // 初始化图表
    initCharts() {
      this.myChart = echarts.init(document.getElementById("main"));
      this.myChart.setOption(this.myOptions);
      this.myChart.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        toolbox: { feature: { saveAsImage: {} } },
        color: ["#7cb5ec", "#52e855", "red", "blue"],
        legend: {
          data: ["前一年度", "本年度", "同比", "环比"],
          textStyle: {
            color: "#fff",
            fontSize: 21,
          },
          // axisLabel: {
          //   fontSize: 24
          // }
        },
        dataZoom: [
          {
            type: "inside", // 内置于坐标系中
            start: 0,
            end: this.end,
          },
        ],
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          splitLine: { show: false },
          axisLine: { lineStyle: { color: "#fff" } },
          axisLabel: {
            fontSize: 12,
          },
          data: this.timeList,
        },
        yAxis: [
          {
            // name: '浓度',
            name: "排名",
            type: "value",
            splitLine: { show: false },
            axisLine: { show: true, lineStyle: { color: "#fff" } },
            nameTextStyle: {
              fontSize: 12,
              align: "left",
            },
            axisLabel: {
              fontSize: 12,
            },
          },
          {
            name: "百分比",
            type: "value",
            splitLine: { show: false },
            axisLine: { lineStyle: { color: "#fff" } },
            nameTextStyle: {
              fontSize: 12,
              align: "right",
            },
            axisLabel: {
              fontSize: 12,
            },
          },
        ],
        series: [
          {
            name: "前一年度",
            type: "bar",
            data: this.lastYear,
          },
          {
            name: "本年度",
            type: "bar",
            data: this.thisYear,
          },
          {
            name: "同比",
            type: "line",
            yAxisIndex: 1,
            data: this.qoq,
          },
          {
            name: "环比",
            type: "line",
            yAxisIndex: 1,
            data: this.mom,
          },
        ],
      });
      window.addEventListener("resize", this.onResize);
    },
    onResize() {
      this.myChart.resize();
    },
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 2, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
};
</script>

