<template>
  <div class="city-ranking-router-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">工作排名</span>
        <CloseBtn routeName="Home" />
      </div>
      <div class="city-ranking-router-body">
        <AreaRanking :tableData="tableData" />
      </div>
    </div>
  </div>
</template>

<script>
import CloseBtn from "@/components/CloseBtn";
import AreaRanking from "./AreaRanking";
import { http } from "@/utils/request";

export default {
  components: {
    CloseBtn,
    AreaRanking,
  },
  data() {
    return {
      activeTab: "2",
      year: null,
      month: null,
      day: null,

      tableData: [],
    };
  },
  watch: {
    $route: {
      handler() {
        // this.id = this.$route.query.id;
        console.log("11111111111", this.$route.query.details);
        // this.getDetail();
        //深度监听，同时也可监听到param参数变化
      },
      deep: true,
    },
  },
  created() {
    this.getNowDate();
    this.getWorkDataList(this.activeTab, this.day, "county");
  },
  methods: {
    getNowDate() {
      this.year = this.$moment().format("YYYY");
      this.month = this.$moment().format("YYYY-MM");
      this.day = this.$moment().subtract(1, "days").format("YYYY-MM-DD");
    },
    tabClick(activeTab) {
      this.activeTab = activeTab;
      let time;
      if (this.activeTab == "2") {
        time = this.day;
      } else if (this.activeTab == "4") {
        time = this.$moment(this.month).format("YYYY-MM-DD");
      } else if (this.activeTab == "5") {
        time = this.$moment(this.year).format("YYYY-MM-DD");
      }
      this.getWorkDataList(this.activeTab, time, "county");
    },
    reduceBtn() {
      if (this.activeTab == "2") {
        this.day = this.$moment(this.day)
          .subtract(1, "days")
          .format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, this.day, "county");
      } else if (this.activeTab == "4") {
        this.month = this.$moment(this.month)
          .subtract(1, "month")
          .format("YYYY-MM");
        let time = this.$moment(this.month).format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, time, "county");
      } else {
        this.year = this.$moment(this.year).subtract(1, "year").format("YYYY");
        let time = this.$moment(this.year).format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, time, "county");
      }
    },
    addBtn() {
      if (this.activeTab == "2") {
        this.day = this.$moment(this.day).add(1, "days").format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, this.day, "county");
      } else if (this.activeTab == "4") {
        this.month = this.$moment(this.month).add(1, "month").format("YYYY-MM");
        let time = this.$moment(this.month).format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, time, "county");
      } else {
        this.year = this.$moment(this.year).add(1, "year").format("YYYY");
        let time = this.$moment(this.year).format("YYYY-MM-DD");
        this.getWorkDataList(this.activeTab, time, "county");
      }
    },

    // 获取工作排名数据
    getWorkDataList(time_type, time, rank_type) {
      // time_type: 2: 日 4: 月 5: 年
      // time: 如果是月，是月的第1天。如果是年，是年的第1天
      // rank_type: city: 市 county: 区

      let json = {
        username: "api",
        password: "a123456b",
        time_type: time_type,
        time: time,
        rank_type: rank_type,
      };
      http
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/ranks",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          if (res.data.code === 2000) {
            this.tableData = res.data.result;
          }
        });
    },
  },
};
</script>

<style lang="scss" >
.city-ranking-router-container {
  width: 1930px;
  height: 969px;
  box-sizing: border-box;
  .main-header {
    position: relative;
  }
  .city-ranking-router-body {
    margin-top: 44px;
    width: 1917px;
    height: 729px;
    margin-top: 120px;
    background: linear-gradient(180deg, rgba(14, 33, 90, 0.4) 0%, #0d1a36 100%);
  }
}
</style>